.board {
    margin: 20px auto;
    max-width: 400px;
}
  
.row {
    display: flex;
}

.congratulations {
    position: fixed;
    bottom: 285px;
    margin-left: 50px;
    font-size: 2rem;
}

.icon {
    color: blue;
}

.draw {
    position: fixed;
    bottom: 285px;
    margin-left: 90px;
    font-size: 2rem;
    color: red;
}

.current_player {
    margin-left: -180px;
    position: fixed;
    bottom: 285px;
    font-size: 2rem;
}

.title {
    position: relative;
    bottom: -60px;
    color: #0072B5;
}
  
.square {
    font-size: 2em;
    font-weight: bold;
    height: 80px;
    line-height: 80px;
    text-align: center;
    width: 80px;
}
  
.square:hover {
    background-color: #eee;
}
  
.status {
    font-size: 1.5em;
    margin-bottom: 100px;
    text-align: center;
}
  
.reset {
    background-color: #4CAF50;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    font-size: 16px;
    margin-top: 70px;
    padding: 10px 20px;
    text-decoration: none;
    margin-left: 65px;
}
  